import { FormEventHandler, useEffect, useState } from "react"
import Card from "../Components/Card"
import FormGroup from "../Components/FormGroup"
import PageTitle from "./Components/PageTitle"
import useUserData from "../Hooks/useUserData"
import { get, patch, put } from "../Functions/Api"
import SetAuth from "../Functions/SetAuth"
import { PopupAlert } from "../Functions/Alerts"

const SettingProfile = () => {
    const [anggotaKel, setAnggotaKel] = useState<DataT[]>()
    const { username, role, no_kk } = useUserData()
    const [nikKepalaKeluarga, setNikKepalaKeluarga] = useState(username)

    const changeNik: FormEventHandler<HTMLSelectElement> = (evt) => {
        setNikKepalaKeluarga(evt.currentTarget.value)
    }

    useEffect(() => {
        const getAnggotaKel = async () => {
            const res = await get(`anggota-keluarga?no_kk=${no_kk}`)
            if (res.ok) setAnggotaKel(res.data)
        }

        getAnggotaKel()
    }, [no_kk])

    const updateAccount: React.FormEventHandler<HTMLFormElement> = async (
        evt
    ) => {
        evt.preventDefault()

        const res = await put(`${username}/edit-akun`, evt.currentTarget)
        if (!res.ok) return

        SetAuth(res)
        PopupAlert.fire({
            icon: "success",
            text: "Berhasil mengupdate akun"
        })
    }

    const updatePattriach: React.FocusEventHandler<HTMLFormElement> = async (
        evt
    ) => {
        evt.preventDefault()

        const res = await patch(
            `anggota-keluarga/kepala-keluarga?no_kk=${no_kk}&nik_kepala_keluarga=${nikKepalaKeluarga}`
        )
        if (!res.ok) return

        SetAuth(res)
        PopupAlert.fire({
            icon: "success",
            text: "Berhasil mengupdate kepala keluarga"
        })
    }

    return (
        <>
            <PageTitle title="Setting Profile" />

            <div className="row mt-5 justify-content-between">
                <div className="col-md-6">
                    <Card
                        cardClassName="p-3"
                        cardHeader={<h4 className="card-title">Update Akun</h4>}
                        cardBody={
                            <form onSubmit={updateAccount}>
                                <FormGroup
                                    label="Username"
                                    name="username"
                                    defaultValue={username}
                                />
                                <FormGroup
                                    label="Password"
                                    name="password"
                                    type="password"
                                />
                                <div>
                                    <button className="btn btn-primary w-100">
                                        Update Akun
                                    </button>
                                </div>
                            </form>
                        }
                    />
                </div>

                <div className="col-md-6">
                    {role === "user" && (
                        <Card
                            cardClassName="p-3"
                            cardHeader={
                                <h4 className="card-title">
                                    Ganti Kepala Keluarga
                                </h4>
                            }
                            cardBody={
                                <form onSubmit={updatePattriach}>
                                    <div className="mb-5">
                                        <label
                                            htmlFor="kepala-keluarga"
                                            className="form-label d-flex justify-content-between"
                                        >
                                            Pilih Kepala Keluarga
                                        </label>
                                        <select
                                            id="kepala-keluarga"
                                            name="nik_kepala_keluarga"
                                            className="form-select"
                                            onChange={changeNik}
                                            value={nikKepalaKeluarga}
                                        >
                                            {!anggotaKel && (
                                                <option>
                                                    Pilih Kepala Keluarga
                                                </option>
                                            )}
                                            {anggotaKel?.map(
                                                ({ nama, nik }, i) => (
                                                    <option key={i} value={nik}>
                                                        {nama}
                                                    </option>
                                                )
                                            )}
                                        </select>
                                    </div>
                                    <FormGroup
                                        label="Nik Kepala Keluarga"
                                        disabled
                                        value={nikKepalaKeluarga}
                                    />
                                    <div>
                                        <button className="btn btn-primary w-100">
                                            Ganti Kepala Keluarga
                                        </button>
                                    </div>
                                </form>
                            }
                        />
                    )}
                </div>
            </div>
        </>
    )
}

export default SettingProfile
